import Section from "./Section";
import Button from "./Button";
import { ReactComponent as DocumentIcon } from "../assets/icons/document.svg";
import { ReactComponent as HtmlIcon } from "../assets/icons/html.svg";
import { ReactComponent as CssIcon } from "../assets/icons/css.svg";
import { ReactComponent as ReactIcon } from "../assets/icons/react.svg";
import { ReactComponent as AngularIcon } from "../assets/icons/angular.svg";
import { ReactComponent as SpringBootIcon } from "../assets/icons/spring-boot.svg";
import { ReactComponent as MySqlIcon } from "../assets/icons/mysql.svg";
import { ReactComponent as MongoDbIcon } from "../assets/icons/mongodb.svg";
import myimage from "../assets/images/me2.png";

import "./Skills.css";

const Skills = (): JSX.Element => (
  <Section id="skills">
    <div className="skills">
      <h2 className="title">Skills</h2>
      <h3 className="subtitle">My technical orientation</h3>
      <div className="skills-content">
        <div>
          <p>
            I am honing my skills at web development, mostly because I am
            passionate about solving challenging problems when it comes to
            developing web applications and creating delightful user
            experiences.
          </p>
          <br />
          <p>
            My skills are oriented around latest web development technologies.
            Using HTML, CSS , Javascript, and frameworks such as React and
            Angular, I enjoy building fast, responsive and interactive web apps.
          </p>
          <br />
          <p>
            Also I am backend oriented on building API services in Java, using
            Spring Boot framework.
          </p>
        </div>
        <div className="skill-items">
          <div className="skill-item">
            <HtmlIcon />
            <span>HTML</span>
          </div>
          <div className="skill-item">
            <CssIcon />
            <span>CSS</span>
          </div>
          <div className="skill-item">
            <ReactIcon />
            <span>React</span>
          </div>
          <div className="skill-item">
            <AngularIcon />
            <span>Angular</span>
          </div>
          <div className="skill-item">
            <SpringBootIcon />
            <span>Spring Boot</span>
          </div>
          <div className="skill-item">
            <MySqlIcon />
            <span>MySQL</span>
          </div>
          <div className="skill-item">
            <MongoDbIcon />
            <span>MongoDB</span>
          </div>
        </div>
      </div>

      <div className="download-cv-container">
        <div className="download-cv-container-left">
          <h2 className="download-cv-container-title">
            Want more information?
          </h2>
          <p>For more details, feel free to download my CV and take a look!</p>
          <Button variant="btn-outline">
            <span>Download CV</span>
            <DocumentIcon />
          </Button>
        </div>
        <img src={myimage} alt="" />
      </div>
    </div>
  </Section>
);

export default Skills;
